import React from "react";
import styled from "styled-components";
import { LabelText, InputContainer, Input } from "../InputRow";
import {
  HeadText,
  HeadTextContainer,
  InputForm,
  InputRowContainer,
  SlideContainer,
  ButtonContainer,
  PrevSlideButton,
  NextSlideButton,
} from "./SetNameJobSlide";

const representText = "  {'\n'}  ";
const addAnotherText = " {'\n'}  "

const SetLocationSlide = ({toggleSlide}) => {
  return (
    <>
      <SlideContainer>
        <HeadTextContainer>
          <HeadText> {"\n"}  </HeadText>
          </HeadTextContainer>
          <InputForm>
            <InputRowContainer>
              <LocationLabelText></LocationLabelText>
              <SubLocationLabelText>
                //     
                .
              </SubLocationLabelText>
            </InputRowContainer>
            <MapContainer></MapContainer>
          </InputForm>
          <ButtonContainer>
          <PrevSlideButton onClick={toggleSlide("prev")}>
            
          </PrevSlideButton>
          <NextSlideButton type="submit">
            
          </NextSlideButton>
        </ButtonContainer>
      </SlideContainer>
    </>
  );
};

export default SetLocationSlide;

const LocationLabelText = styled(LabelText)`
  margin-bottom: 10px;
`;

const SubLocationLabelText = styled(LabelText)`
  font-size: 13px;
  line-height: 21px;
  white-space: pre-wrap;
`;

const MapContainer = styled(InputContainer)`
  width: 400px;
  height: 400px;
  border-radius: 16px;
`;
